import React, {useEffect} from 'react'
import {Grid, Paper, Box, Typography, Icon} from "@material-ui/core";
import Aos from "aos";
import "aos/dist/aos.css";
import {makeStyles} from "@material-ui/core/styles";
import {motion} from "framer-motion";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles";

const theme = createMuiTheme({
    typography: {
      
    }
  });

const pathVariants = {
    hidden: {
      opacity: 0,
      pathLength: 0
    },
    visible: {
      opacity: 1,
      pathLength: 1,
      transition: {
        duration: 3,
        ease: "easeInOut",
        yoyo: Infinity,
        repeatDelay: 0.5
      }
    }
  }

// CSS STYLES
const useStyles = makeStyles(theme=> ({
    rootGrid: {
        width: "100%",
        margin: "0px"
      },
    gridLayout1: {
        backgroundColor: "black",
        paddingTop:"50px",
        paddingLeft: "20px"
    },
    gridLayout2: {
        backgroundColor: "black",
        paddingTop:"60px",
        textAlign: "center" 
    },
    gridLayout3: {
        backgroundColor: "black",
        paddingTop:"60px",
        textAlign: "justify" 
    }
}));

const About = () => { {
    const classes = useStyles();
    useEffect(() => {
        Aos.init({ 
            offset: 120,
            duration: 500,
            delay: 0
        });
    }, []);
    return (
        <MuiThemeProvider theme={theme}>
            <div style={{width: "100%", height: "1000px", background: "black"}}>
            <div style={{marginLeft:"20px", marginRight: "20px", overflowX: "hidden"}}>
                <Grid container spacing={3} className={classes.rootGrid}>  
                    <Grid item xs={12} md={6}>
                        <Paper data-aos="fade-right" className={classes.gridLayout1}>
                        <Typography  style={{color: "white", fontSize:"8vw", fontWeight:"600"}}>
                            Hello.
                        </Typography>
                        <Typography  style={{color: "white", fontSize:"8vw", fontWeight:"600"}}>
                            I'm  <span style={{color: "#bf360c"}}>     Sacha</span>
                        </Typography>
                        </Paper>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Paper data-aos="fade-left" className={classes.gridLayout2}>
                        <motion.svg 
                        height="23vw" 
                        viewBox="-16 -18 533.33331 533" 
                        width="23vw" 
                        xmlns="http://www.w3.org/2000/svg"
                        variants={pathVariants}
                        initial='hidden'
                        animate='visible'
                        >
                        <motion.path fill="none" stroke="#bf360c" strokeWidth="6" d="m248.429688 25.082031c39.289062 0 78.640624 11.152344 113.800781 32.253907 22.300781 13.417968 42.339843 30.28125 59.363281 49.972656l-58.738281-1.9375c-6.898438-.230469-12.675781 5.179687-12.902344 12.082031-.226563 6.898437 5.179687 12.679687 12.082031 12.902344l81.269532 2.683593c4.667968 1.921876 10.035156.847657 13.605468-2.726562 3.566406-3.574219 4.632813-8.941406 2.703125-13.609375l-2.675781-81.25c-.15625-6.902344-5.878906-12.371094-12.78125-12.21875-6.898438.15625-12.375 5.878906-12.21875 12.777344 0 .09375 0 .179687.007812.269531l1.546876 46.875c-17.195313-18.375-36.847657-34.285156-58.398438-47.265625-39.042969-23.425781-82.84375-35.8085938-126.664062-35.8085938-70 0-134.539063 28.8632808-181.703126 81.2695308-41.78125 46.425782-66.726562 108.914063-66.726562 167.160157 0 6.902343 5.59375 12.5 12.5 12.5s12.5-5.597657 12.5-12.5c0-52.238281 22.542969-108.476563 60.308594-150.433594 42.367187-47.066406 100.292968-72.996094 163.121094-72.996094zm0 0"   variants={pathVariants}/>
                        <motion.path fill="none" stroke="white" strokeWidth="6" d="m487.5 236.011719c-6.90625 0-12.5 5.59375-12.5 12.5 0 52.234375-22.542969 108.476562-60.308594 150.4375-42.367187 47.066406-100.292968 72.988281-163.121094 72.988281-39.289062 0-78.640624-11.144531-113.800781-32.246094-22.300781-13.421875-42.339843-30.285156-59.363281-49.972656l58.738281 1.9375c6.898438.222656 12.675781-5.1875 12.902344-12.085938.226563-6.898437-5.179687-12.675781-12.082031-12.90625l-81.269532-2.679687c-4.667968-1.921875-10.035156-.84375-13.605468 2.726563-3.566406 3.574218-4.632813 8.9375-2.703125 13.605468l2.675781 81.25c.15625 6.90625 5.878906 12.378906 12.78125 12.222656 6.898438-.152343 12.375-5.875 12.21875-12.777343 0-.089844 0-.175781-.007812-.269531l-1.546876-46.875c17.195313 18.378906 36.847657 34.28125 58.398438 47.269531 39.042969 23.429687 82.84375 35.808593 126.664062 35.808593 70 0 134.539063-28.859374 181.703126-81.265624 41.78125-46.429688 66.726562-108.917969 66.726562-167.167969 0-6.90625-5.59375-12.5-12.5-12.5zm0 0"   variants={pathVariants}/>
                        </motion.svg>
                        </Paper>
                    </Grid>
                    <Grid item xs={12} md={12}>
                        <Paper data-aos="fade-right" className={classes.gridLayout3}>
                        <Typography  style={{color: "white", fontSize:"18px", fontWeight:"100"}}>
                        Since beginning my journey as a freelance designer nearly 10 years ago, I've done remote work for agencies, consulted for startups, and collaborated with talented people to create digital products for both business and consumer use. I'm quietly confident, naturally curious, and perpetually working on improving my chops one design problem at a time.
                        </Typography>
                        </Paper>
                    </Grid>
                </Grid>
            </div>
            </div>
        </MuiThemeProvider>
    )
}}

export default About

