import React, {useEffect,useState} from 'react';
import {
Typography,
Avatar,
Grid,
Box,
IconButton,
Divider,
Paper
} from "@material-ui/core";
/**import avatar from "../images/profilepic.png"; **/
import Typed from "react-typed";
import {makeStyles} from "@material-ui/core/styles";
import {motion} from "framer-motion";
import {Link} from "react-scroll";
import arrow from "../images/arrow.png";
import background from "../images/background.jpg";
import Particles from "react-particles-js";
import concordiaImg from "../images/concordia-university.jpg"
import Contacts from "./Contacts";
import About from "./About";

// CSS STYLES
const useStyles = makeStyles(theme=> ({
    title: {
        position: "absolute",
        transform: "translate(-50%, -50%)", 
        top: "35%",
        left: "22%",
        color: "black",
        fontWeight: "500",
        fontSize: "1.5em",
        zIndex: -1
    },
    container: {
        position: "relative",
        width: "100%",
        height: "100vh",
        textAlign: "center",
        background: "url("+ background +")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "contain",
        backgroundSize: "cover",
        backgroundPosition: "center",
        zIndex: 1
    },
    particlesCanvas: {
        position: "absolute",
        opacity: "0.9",
        right: "0px"     
    },
}));

const Home = () => {
    const classes = useStyles();
    return (
        <>
        <Box className={classes.container} component="div" id="home">
        <Particles canvasClassName={classes.particlesCanvas}
            params={{
                "particles": {
                    line_linked: {
                        "color": "#f57c00"
                    },
                    
                    "color": {
                        "value": "#ef5350"
                    },
                    "number": {
                        "value": 30
                    },
                    "size": {
                        "value": 8
                    }
                },
                "interactivity": {
                    "events": {
                        "onhover": {
                            "enable": true,
                            "mode": "repulse"
                        }
                    }
                }
            }} /> 
            <Typography className={classes.title}>
                <Typed 
                  strings={[""]} 
                  typeSpeed={40}
                  backSpeed={30}
                  loop
                />
            </Typography>
            <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
           
            <IconButton 
                component={Link} to="about" smooth={true} duration={1000} offset={-60} style={{position: "absolute", top:"80%", left:"50%", transform: "translateX(-50%)"}}
            >
             <img src={arrow} style={{width: '40px', height: '40px'}}></img> 
            </IconButton>
        </Box>
        <div id="about">
          <About/>
        </div>
        <div id="contact">
          <Contacts/>
        </div>
        </>
    )
}

export default Home
